'use strict';

const initialState = {
  reversed: '',
  posts: [],
  isLoaded: false,
  error: null,
  isLoggedIn: false,
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case 'REVERSE':
      return {
        ...state,
        reversed: action.payload.split('').reverse().join('')
      };
    case 'SUCCESFULLY_LOADED_POSTS':
      return {
        ...state,
        posts: action.payload,
        isLoaded: true,
      };
    case 'UNSUCCESFULLY_LOADED_POSTS':
      return {
        ...state,
        error: action.payload,
        isLoaded: true
      };
    case 'SET_LOGIN_STATUS':
      return {
        ...state,
        isLoggedIn: action.payload
      };
    case 'SET_LOGOUT_STATUS':
      return {
        ...state,
        isLoggedIn: action.payload
      };
  }
  return state;
};

export default reducer;
