import React from 'react';
import PropTypes from 'prop-types';
import styles from './style.css';
import showRelativeTime from '../../utilities/relativeTime/relativeTime';

const Post = (props) => {
  const { title, channel_name, channel_image, content, time_entered } = props.item;
  return (
    <div className={styles.container}>
      <div className={styles.sidebar}>
        <div className={styles.votes}>
          <i className="fas fa-arrow-up"></i>
          <p>36</p>
          <i className="fas fa-arrow-down"></i>
        </div>
      </div>
      <div className={styles.content}>
        <div className={styles['content-header']}>
          <div className={styles.metadata}>
            <img src={channel_image} />
            <p className={styles['channel-name']}>r/{channel_name}</p>
            <p>|</p>
            <p>Posted</p>
            <p className={styles.timestamp}>{showRelativeTime(time_entered)}</p>
          </div>
          <div className={styles.title}>
            <h1>{title}</h1>
          </div>
        </div>
        <div className={styles['text-content']}>
          <p>
            {content}
          </p>
        </div>
      </div>
    </div>
  );
};

Post.propTypes = {
  item: PropTypes.shape({
    id: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    content: PropTypes.string.isRequired,
    channel_name: PropTypes.string.isRequired,
    channel_image: PropTypes.string,
    time_entered: PropTypes.number.isRequired,
  })
};

export default Post;
